import React, { useState } from 'react'
import './Header.css'
import SearchIcon from '@mui/icons-material/Search';
import CloseIcon from '@mui/icons-material/Close';
// import LanguageIcon from '@mui/icons-material/Language';
// import ExpandMoreIcon from '@mui/icons-material/ExpandMore';
import { Avatar } from '@mui/material';
import { Link } from 'react-router-dom';
import SpeechRecognition from 'react-speech-recognition';
// import './GroupChat';

import "./Button.css";

function Header({placeholder, data}) {

    const [filteredData, setFilteredData] = useState([]);
    const [wordEntered, setWordEntered] = useState("");

    const handleFilter = (event) => {
        const searchWord = event.target.value
        setWordEntered(searchWord);
        const newFilter = data.filter((value) => {
            return value.title.toLowerCase().includes(searchWord.toLowerCase()); 
        });

        if (searchWord === ""){
            setFilteredData([]);
        }
        else {
            setFilteredData(newFilter);
        }
        
    };

    const clearInput = () => {
        setFilteredData([]);
        setWordEntered("");
    }


    return (
        <div className = 'header'>
            <Link to = '/'>
                <img
                    className="header__icon"
                    src = "speedy_rent_logo.png"
                    alt = " "
                    
                />
            </Link>
    
            <div className='header__center'>
                <input type = "text" placeholder = {placeholder} value = {wordEntered} onChange = {handleFilter}/>
                {filteredData.length === 0 ? <SearchIcon/> : <CloseIcon id = "clearBtn" onClick = {clearInput}/>}
                

            {filteredData.length !== 0 && 
            (    
                <div className = "dataResult">
                        {filteredData.slice(0, 15).map((value, key) => {
                            return (
                            <a className = "dataItem" href = {value.link} target = "_blank" id = "dataItemTag">
                                
                                <p>{value.title}</p>
                            </a> 
                            );
                        })}
                </div>
            )}



                
            </div>

           

            <div className='header__right'>
            <button type="button" class= "button" style={{maxWidth:"300px",position:"fixed", top:0, right:400,display:"block"}} onClick={SpeechRecognition.startListening}>Speak</button>

                <p> Become an owner today!</p>
                {/* <LanguageIcon />
                <ExpandMoreIcon /> */}
                <Link to = '/signup'>
                    <Avatar/>
                </Link>
            </div>
        </div>
    )
}

export default Header
